<script>

import VueDaumMap from 'vue-daum-map'

export default {
  name: "Map",
  components: {VueDaumMap},
  data() {
    return {
      scrollX: 0,
      width: 320,
      appKey: '8bd60f2a692d1bf9b4b879b8299f6f46', // 테스트용 appkey 	467705e8b9de11df767cc296d8e69955 //13820f5d51d54f2bfb5203278b7bf8b9
      center: {lat: 37.50139779887541, lng: 126.77758264154043}, // 지도의 중심 좌표 level: 3, // 지도의 레벨(확대, 축소 정도),
      mapTypeId: VueDaumMap.MapTypeId.NORMAL, // 맵 타입
      libraries: ["drawing"], // 추가로 불러올 라이브러리
      map: null, // 지도 객체. 지도가 로드되면 할당됨.
      daum: null, // 다음 API 객체. 지도가 로드되면 할당됨.
      //////////////
      tmapUrl: "",
      kakaoTaxiUrl: "",
      navermapUrl: "",
      kakaomapUrl: ""

    };
  },
  mounted() {
    this.makeUrls()
  },
  methods: {
    // 지도가 로드 완료되면 load 이벤트 발생
    onLoad(map, daum) {
      this.map = map;
      this.daum = daum;

      // 지도를 클릭한 위치에 표출할 마커입니다
      var marker = new kakao.maps.Marker({
        // 지도 중심좌표에 마커를 생성합니다
        position: map.getCenter()
      });
      // 지도에 마커를 표시합니다
      marker.setMap(map);
    },
    makeUrls() {
      // 장소데이터의 이름정보 불러온 뒤
      const locationName = "부천 중동 쎄쎄쎄"
      this.tmapUrl = "tmap://search?name=" + locationName
      this.kakaoTaxiUrl = "https://t.kakao.com/launch?type=taxi&amp;dest_lat=37.50145860034474&amp;dest_lng=126.77757397931599&amp;ref=localweb"
      this.navermapUrl = "nmap://search?query=" + locationName + "&appname=yootaejoen.github.io/yooeunbirthday"
      this.kakaomapUrl = "kakaomap://search?q=" + locationName
    }
  }
};
</script>

