<template>
  <div class="celebrate">
    <img src="~@/assets/images/goose.png" class="goose" />
    <div class="description">Our family's happiest moment</div>
    <h2 class="h2">유은이의 행복한 순간들</h2>


    <!--
    <img src="~@/assets/images/cat.png" class="cat" />
    <h2 class="h2">둘째. 문구 생각중</h2>
    <p class="black">
    
    </p>
    -->

    <div class="image-album">
      <div class="images">
        <img
          class="image"
          v-for="imageUrl in imageUrls"
          v-bind:key="imageUrl.index"
          v-bind:src="imageUrl"
        />
      </div>
      <div v-if="imageUrls.length > 1" class="image-circle-wrapper">
        <!--imageUrls.slice(Math.max(imageUrls.length - 5, 0))-->
        <div
          class="image-circle"
          v-for="(imageUrl, index) in imageUrls.slice(Math.max(imageUrls.length - 5, 0))"
          v-bind:key="imageUrl.index"
          v-bind:class="{ activeImg: index == curPos}"
        ></div>
      </div>
    </div>

    <br />
    <p class="black">
    
    </p>

  </div>
</template>

<script>
export default {
  name: "Celebrate",
  data() {
    return {
      curPos: 0,
      postion: 0,
      start_x: 0,
      end_x: 0,
      IMAGE_WIDTH: 0,
      images: null,
      imageUrls: [
        "https://raw.githubusercontent.com/YootaeJeon/yooeunbirthday/main/gallery/yooeun (1).jpg?",
        "https://raw.githubusercontent.com/YootaeJeon/yooeunbirthday/main/gallery/yooeun (2).jpg?",
        "https://raw.githubusercontent.com/YootaeJeon/yooeunbirthday/main/gallery/yooeun (3).jpg?",
        "https://raw.githubusercontent.com/YootaeJeon/yooeunbirthday/main/gallery/yooeun (4).jpg?",
        "https://raw.githubusercontent.com/YootaeJeon/yooeunbirthday/main/gallery/yooeun (5).jpg?",
        "https://raw.githubusercontent.com/YootaeJeon/yooeunbirthday/main/gallery/yooeun (5-1).jpg?",
        "https://raw.githubusercontent.com/YootaeJeon/yooeunbirthday/main/gallery/yooeun (6).jpg?",
        "https://raw.githubusercontent.com/YootaeJeon/yooeunbirthday/main/gallery/yooeun (7).jpg?",
        "https://raw.githubusercontent.com/YootaeJeon/yooeunbirthday/main/gallery/yooeun (8).jpg?",
        "https://raw.githubusercontent.com/YootaeJeon/yooeunbirthday/main/gallery/yooeun (9).jpg?",
        "https://raw.githubusercontent.com/YootaeJeon/yooeunbirthday/main/gallery/yooeun (10).jpg?",
        "https://raw.githubusercontent.com/YootaeJeon/yooeunbirthday/main/gallery/yooeun (11).jpg?",
        "https://raw.githubusercontent.com/YootaeJeon/yooeunbirthday/main/gallery/yooeun (12).jpg?",
        "https://raw.githubusercontent.com/YootaeJeon/yooeunbirthday/main/gallery/yooeun (13-1).jpg?",
        "https://raw.githubusercontent.com/YootaeJeon/yooeunbirthday/main/gallery/yooeun (16).jpg?",
        "https://raw.githubusercontent.com/YootaeJeon/yooeunbirthday/main/gallery/yooeun (17).jpg?",
        "https://raw.githubusercontent.com/YootaeJeon/yooeunbirthday/main/gallery/yooeun (18).jpg?",
        "https://raw.githubusercontent.com/YootaeJeon/yooeunbirthday/main/gallery/yooeun (19).jpg?",
        "https://raw.githubusercontent.com/YootaeJeon/yooeunbirthday/main/gallery/yooeun (20).jpg?",
        "https://raw.githubusercontent.com/YootaeJeon/yooeunbirthday/main/gallery/yooeun (21).jpg?",
        "https://raw.githubusercontent.com/YootaeJeon/yooeunbirthday/main/gallery/yooeun (15).jpg?",
        "https://raw.githubusercontent.com/YootaeJeon/yooeunbirthday/main/gallery/yooeun (13).jpg?",
        "https://raw.githubusercontent.com/YootaeJeon/yooeunbirthday/main/gallery/yooeun (23).jpg?",
        "https://raw.githubusercontent.com/YootaeJeon/yooeunbirthday/main/gallery/yooeun (25).jpg?",
        "https://raw.githubusercontent.com/YootaeJeon/yooeunbirthday/main/gallery/yooeun (26).jpg?",
        "https://raw.githubusercontent.com/YootaeJeon/yooeunbirthday/main/gallery/yooeun (26-1).jpg?",
        "https://raw.githubusercontent.com/YootaeJeon/yooeunbirthday/main/gallery/yooeun (27).jpg?",
        "https://raw.githubusercontent.com/YootaeJeon/yooeunbirthday/main/gallery/yooeun (27-1).jpg?",
        "https://raw.githubusercontent.com/YootaeJeon/yooeunbirthday/main/gallery/yooeun (28).jpg?",
        "https://raw.githubusercontent.com/YootaeJeon/yooeunbirthday/main/gallery/yooeun (29).jpg?",
        "https://raw.githubusercontent.com/YootaeJeon/yooeunbirthday/main/gallery/yooeun (31).jpg?",
        "https://raw.githubusercontent.com/YootaeJeon/yooeunbirthday/main/gallery/yooeun (32).jpg?",
        "https://raw.githubusercontent.com/YootaeJeon/yooeunbirthday/main/gallery/yooeun (14).jpg?",
        "https://raw.githubusercontent.com/YootaeJeon/yooeunbirthday/main/gallery/yooeun (33).jpg?",
        "https://raw.githubusercontent.com/YootaeJeon/yooeunbirthday/main/gallery/yooeun (34).jpg?",
        "https://raw.githubusercontent.com/YootaeJeon/yooeunbirthday/main/gallery/yooeun (22).jpg?"
      ],
    };
  },
  computed: {
    getImageWidth: () => {
      const imgWidth = document.querySelector(".images").offsetWidth;
      return imgWidth;
    },
  },
  mounted: function () {
    this.IMAGE_WIDTH = this.getImageWidth;
    this.images = document.querySelector(".images");
    this.images.addEventListener("touchstart", this.touch_start);
    this.images.addEventListener("touchend", this.touch_end);
  },
  methods: {
    prev() {
      if (this.curPos > 0) {
        this.postion += this.IMAGE_WIDTH;
        this.images.style.transform = `translateX(${this.postion}px)`;
        this.curPos = this.curPos - 1;
      }
    },
    next() {
      if (this.curPos < this.imageUrls.length-1) {
        this.postion -= this.IMAGE_WIDTH;
        this.images.style.transform = `translateX(${this.postion}px)`;
        this.curPos = this.curPos + 1;
      }
    },

    touch_start(event) {
      this.start_x = event.touches[0].pageX;
    },

    touch_end(event) {
      this.end_x = event.changedTouches[0].pageX;
      if (this.start_x > this.end_x) this.next();
      else this.prev();
    },
  },
};
</script>

